		},
		change(e) {
			this.num = e
			console.log(e)
		}
	},
	onPullDownRefresh() {
		console.log('refresh');
		this._load();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
